    @supports (font-variation-settings: 'wdth' 115) {
      h1 {
        font-family: variable-font-family, sans-serif;
      }
    }

    .nav{
      overflow: hidden;
      z-index: 2;
      background-color: black;
      font-weight: 900;
      position: fixed;
      width: 100%;
      flex-direction: row;
      height: 90px;
      box-shadow: 0px 3px 0px 0px rgb(75, 75, 75);
    }

    .logoWrapper{
      height: 100%;
      flex: 3 1;
    }

    .linkWrapper{
      flex: 4 1;
      display: block;
      margin:0px;
      align-content: center;
      height: 100%;
    }

    .imageWrapper{
      flex: 3 1;
    }

    .image{
      margin-left: auto;
      height: 80px;
      object-fit: contain;
    }

    .logo{
      margin-bottom: auto;
      max-height: 60px;
    }

    @media (max-width: 1400px) {
        .logo{
          max-width: 300px;
          margin: 0;
        }
    }

    ul{
      margin:0px;
      padding:0px;
      height: 100%;
      max-width: 1600px;
      display: flex;
      flex-direction: row;
      flex-grow: 1;
      list-style-type: none;
      align-items: center;
    }

    li{
      flex: 1 1 auto;
      text-decoration: none;
      padding : 30px;
      font-size: 30px;
      font-family: Arial, Helvetica, sans-serif;
    }

    .nav a{
      display: flex;
      justify-content: center;
      align-items: center;
      height:100%;
    }
    .nav .logoWrapper a{
      display: flex;
      justify-content: left;
      height:100%;
    }
    .nav .imageWrapper a{
      display: flex;
      align-items: center;
      height:100%;
    }
    .nav a, .nav a:visited {
      color : white;
      text-decoration: none;
    }

    .nav a:hover, .nav a:visited:hover {
        color : white;
        text-decoration: none;
    }

    @media ( max-width:1200px ) {
        .imageWrapper { display: none; }
    }
    @media ( max-width:950px ) {
        .linkWrapper { display: none; }
    }
